import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { getCertificateTypeById, createCertificateType, updateCertificateType } from 'services/certificateType';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formAllCertificates' })
class FormAllCertificates extends Component {
  state = {
    certId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    console.log(match.params.id);
    if (match.params.id) {
      this.setState({ certId: match.params.id });
      this.getCertById(match.params.id);
    }
  };

  getCertById = async id => {
    const res = await getCertificateTypeById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { certId } = this.state;
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        try {
          if (!certId) {
            await createCertificateType(values);
          } else {
            await updateCertificateType({ id: certId, ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          console.log(error);
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { certId } = this.state;
    console.log(this.state);
    return (
      <Card title={`${certId ? 'Cập nhật' : 'Thêm'} chứng chỉ`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} >
            <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={certId !== false} />)}
            </Form.Item>
            <Form.Item hasFeedback label="Tên chứng chỉ" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label=" " {...formItemLayout}>
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAllCertificates {...props} />
  </Page>
));



