import { request } from 'utils';

const prefix = '/api/categories';


export const getCertificateTypes = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/CertificateType',
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });
  
export const getCertificateTypeById = (id) =>
  request({
  prefix,
  url: '/CertificateType/GetById',
  method: 'GET',
  params: { id },
  data: {}
  });
  
export const updateCertificateType = ({ id, label } = {}) =>
  request({
    prefix,
    url: '/CertificateType',
    method: 'PUT',
    params: {},
    data: { id, label }
  });


export const createCertificateType = ({ id, label } = {}) =>
  request({
    prefix,
    url: '/CertificateType',
    method: 'POST',
    params: {},
    data: { id, label }
  });

export const deleteCertificateType = (id) =>
  request({
    prefix,
    url: '/CertificateType',
    method: 'DELETE',
    params: {},
    data: { id }
  });


